:root {
  --main-font: #363738;
  --add-font: #6B6B6C;
  --theme-main: #FFFFFF;
  --theme-secondary: #0B7572;
  --underline-color: #FFCE50;
}

/* exo-2-regular - latin_cyrillic */

@font-face {
  font-family: 'Exo 2';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(""), url("../fonts/exo2/exo-2-v18-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/exo2/exo-2-v18-latin_cyrillic-regular.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

/* exo-2-600 - latin_cyrillic */

@font-face {
  font-family: 'Exo 2';
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: local(""), url("../fonts/exo2/exo-2-v18-latin_cyrillic-600.woff2") format("woff2"), url("../fonts/exo2/exo-2-v18-latin_cyrillic-600.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

/* pt-sans-regular - latin_cyrillic */

@font-face {
  font-family: 'PT Sans';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(""), url("../fonts/PTSans/pt-sans-v16-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/PTSans/pt-sans-v16-latin_cyrillic-regular.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

/* pt-sans-700 - latin_cyrillic */

@font-face {
  font-family: 'PT Sans';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local(""), url("../fonts/PTSans/pt-sans-v16-latin_cyrillic-700.woff2") format("woff2"), url("../fonts/PTSans/pt-sans-v16-latin_cyrillic-700.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

:root {
  --content-width: 1318px;
  --mob-padding: 39px;
  --gutter: 15px;
}

.container {
  max-width: var(--content-width);
  padding-left: var(--mob-padding);
  padding-right: var(--mob-padding);
  margin-left: auto;
  margin-right: auto;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  font-family: 'PT Sans', 'Exo 2', sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.header {
  position: relative;
  padding-top: 110px;
  padding-bottom: 173px;
}

.header::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  background-color: var(--theme-secondary);
  width: 42%;
  height: 100%;
  z-index: -100;
}

.header__wrap {
  display: flex;
}

.header__txt-block {
  max-width: 50%;
}

.header__title {
  margin-top: 80px;
  font-family: 'Exo 2';
  font-weight: 600;
  font-size: 52px;
  line-height: 125%;
  color: var(--main-font);
  margin-bottom: 30px;
}

.header__desc {
  padding: 0;
  margin: 0;
  color: var(--add-font);
}

.header__link {
  display: inline-block;
  margin-top: 30px;
  padding-bottom: 5px;
  text-decoration: none;
  font-weight: bold;
  color: var(--main-font);
  border-bottom: 3px solid var(--underline-color);
}

.promo {
  position: relative;
  margin-bottom: 200px;
}

.promo__cards-wrap {
  display: flex;
  position: absolute;
  bottom: 0;
  transform: translate(0, 71%);
  padding: 40px 0;
  margin-right: 39px;
  max-width: 1240px;
  border-radius: 20px;
  background-color: var(--theme-main);
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.15);
  z-index: 10;
}

.promo__card {
  position: relative;
  padding: 0 80px;
  border-right: 1px solid #D7D7DD;
}

.promo__card:last-child {
  border: none;
}

.promo__card-img {
  margin-bottom: 24px;
  max-width: 92px;
}

.promo__card-title {
  margin-top: 0;
  margin-bottom: 15px;
  font-family: 'Exo 2';
  font-weight: 600;
  font-size: 20px;
  line-height: 135%;
  max-width: 228px;
  color: var(--main-font);
}

.promo__card-desc {
  margin: 0;
  font-size: 16px;
  line-height: 150%;
  color: var(--add-font);
}

@media (max-width: 1200px) {
  .header__txt-block {
    max-width: 80%;
  }

  .header__title {
    margin-top: 0;
    font-size: 44px;
  }

  .promo__cards-wrap {
    position: static;
    flex-direction: column;
    box-shadow: none;
    border: none;
    margin-right: 0;
    transform: translate(0, 0);
  }

  .promo__card {
    margin-bottom: 16px;
    border: none;
    border-radius: 20px;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.12);
    padding: 35px 100px 47px 156px;
  }

  .promo__card:last-child {
    margin-bottom: 0;
  }

  .promo__card-img {
    position: absolute;
    margin-bottom: 0;
    top: 32px;
    left: 32px;
  }

  .promo__card-title {
    max-width: none;
  }
}

@media (max-width: 920px) {
  .header {
    padding-top: 80px;
    padding-bottom: 0;
  }

  .header::before {
    width: 21%;
  }

  .header__wrap {
    flex-direction: column;
  }

  .header__link {
    margin-top: 60px;
    margin-bottom: 55px;
  }
}

@media (max-width: 425px) {
  .container {
    padding-left: var(--gutter);
    padding-right: var(--gutter);
  }

  .header {
    padding-bottom: 180px;
  }

  .header::before {
    width: 100%;
  }

  .header__title {
    font-size: 28px;
    color: white;
  }

  .header__desc {
    font-size: 15px;
    color: white;
  }

  .header__link {
    font-size: 15px;
    color: white;
  }

  .promo__cards-wrap {
    position: absolute;
    align-items: flex-start;
    transform: translate(0, 80%);
    border-radius: 20px;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.15);
    margin-right: 15px;
    padding-top: 12px;
  }

  .promo__card {
    box-shadow: none;
    padding-top: 12px;
    padding-left: 32px;
    padding-bottom: 12px;
    padding-right: 24px;
    margin-bottom: 0;
  }

  .promo__card-img {
    position: static;
    max-width: 68px;
    margin-bottom: 16px;
  }

  .promo__card-title {
    margin-bottom: 12px;
    font-size: 18px;
  }

  .promo__card-desc {
    font-size: 14px;
  }
}